            <li>
              <img src="../assets/img/user_icon.png" alt="">
              <img src="../assets/img/user_icon_.png" alt="">
            </li>
            <li>
              <img src="../assets/img/box_icon.png" alt="">
              <img src="../assets/img/box_icon_.png" alt="">
            </li>
          </ul>
          <!--            更多-->
          <div class="more">
            <div class="line"></div>
            <div class="line"></div>
            <div class="line"></div>
          </div>
          <div class="more_box">
            <div class="feedback">意见反馈</div>
            <div class="about">关于作者</div>
          </div>

        </div>
        <!--        中间-->
        <div class="nav_right">
          <!--            搜索栏-->
          <div class="search">
            <input type="text" placeholder="搜索">
            <input type="button" value="+">
            <span class="search_btn"></span>
          </div>
          <!--            好友列表-->
          <div class="friends">
            <ul class="friends_list">
              <li class="default" @click="deName($event)">
                <div class="friend">
                  <div class="friend_info">
                    <img src="../assets/img/room.jpg" alt="" width="40px" height="40px">
                    <div class="user_name">聊天室</div>
                  </div>
                  <div class="friend_time">
                    20:33
                  </div>
                </div>
              </li>
              <li @click="choose($event)" class="user_info" v-for="item ,index in onlineList">
                <div class="friend">
                  <div class="friend_info">
                    <img :src="item.imgUrl" alt="" width="40px" height="40px">
                    <div class="user_name">{{item.name}}</div>
                  </div>
                  <div class="friend_time">
                    {{item.time}}
                  </div>
                </div>
              </li>
            </ul>
          </div>
        </div>
      </div>
      <div class="right">
        <div class="head_box">
          <div class="chat_title">聊天室</div>
          <div class="middle">登录了</div>
          <div class="quit">
            <p @click="quit">×</p>
          </div>
        </div>
        <div class="msg_box">
          <!--            查看更多消息-->
          <div class="more_msg">
            <a href="javascript:;">查看更多消息</a>
          </div>
          <!--            消息列表-->
          <ul class="content" id="content">
            <li class="msg other_msg" v-for="item,index in msgList"
              :class="{'my_msg':userInfo.name==msgList[index].name}">
              <div class="msg_head">
                <img :src="item.imgUrl" alt="" width="100%">
              </div>
              <div class="msg_content">
                <div class="name">{{item.name}}
                  <span class="time">{{item.time}}</span>
                </div>
                <div class="text" v-html="item.text">
                  {{item.text}}
                </div>
              </div>
            </li>

            <!-- <span class="face face26"></span> -->
          </ul>
        </div>
        <div class="send_box">
          <textarea name="words" v-model="text" id="words" @focus="hide"></textarea>
          <div class="emoji" @click="show">
            <div class="emoji_detail" v-show="flag">
              <ul>
                <li @click="add(index)" v-for="item,index in emojiList" class="face" :class="item.faceID"
                  :title="item.title">
                </li>
              </ul>
            </div>
          </div>
          <div id="send">
            <button class="send" @click="send">发送</button>
            <div class="tip" v-show="tag">不能发送空白消息</div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

