  mounted() {
    //获取登录数据发送给服务端
    this.userInfo = this.$route.params;
    socket.emit("userinfo", this.userInfo);

    //接收服务端发来的用户列表
    socket.on("online", onlineList => {
      this.onlineList = onlineList.list;
      console.log(this.onlineList);
    });
    socket.on("outline", onlineList => {
      this.onlineList = onlineList.list;
    });
    //接受后端传来的消息
    socket.on("msg", msg => {
      // console.log("当前消息", msg);
      // if (this.allMsgList[this.curChat]) {
      //   this.allMsgList[this.curChat].push(msg);
      // } else {
      //   this.allMsgList[this.curChat] = [];
