      this.msgList.push(msg);
      // this.allMsgList[this.curChat]=msg;
      // console.log("消息", this.allMsgList);
    });
  },
  methods: {
    choose(e) {
      this.curChat = e.target.textContent;
    },
    deName(e) {
      this.curChat = "defalt";
    },
    send() {
      //文本内容发送给服务端
      if (this.text == "") {
        this.tag = true;
        setTimeout(() => {
          this.tag = false;
        }, 2000);
      } else {
        socket.emit("message", {
          name: this.userInfo.name,
          text: this.text,
          time: this.userInfo.time,
          imgUrl: this.userInfo.imgUrl,
          classArr: this.classArr,
          curChat: this.curChat
        });
      }
      // this.allMsgList[this.curChat] = this.msgList;
      // console.log(this.allMsgList);
      // console.log(this.curChat);

      this.text = "";
      this.classArr = [];
    },
    show() {
      this.flag = !this.flag;
    },
    hide() {
      this.flag = false;
    },
    add(index) {
      this.classArr.push(this.emojiList[index].faceID);
      this.text += "[" + this.emojiList[index].title + "]";
    },
    quit() {
      this.$router.push("/");
    }
